const getText = function*(id) {
    let lastId;
    let index = -1;
    const textArray = _.values(texts);
    while(true) {
        if(id !== lastId) {
            index = index < textArray-2 ? index+1 : 0;
            lastId = id;
        }
        yield textArray[index];
    }
}

export default getText;



